<template lang="html">
  <article>
      <h1 class="page-header"></h1>
      <section class="lang-md">
# Markdown

## 

****




      </section>
  </article>
</template>

<script>
import marked from 'marked';
marked.setOptions({
    renderer: new marked.Renderer(),
    gfm: true,
    tables: true,
    breaks: false,
    pedantic: false,
    sanitize: false,
    smartLists: true,
    smartypants: false
});
export default {
    mounted() {
        const text = $('.lang-md').html();

        $('.lang-md').html(marked(text.trim()));
    }
};
</script>

<style lang="css">
</style>



