<template lang="html">
<div>
    <h1 class="page-header"></h1>
    <h2></h2>
    <div>
        <p>
            <button type="button" class="btn btn-default">Default</button><br>
            
            <blockquote>
                <a href="javascript:;" class="clip" data-clipboard-text="Just because you can doesn't mean you should  clipboard.js">
                  
                </a>
            </blockquote>
            <pre><code class="html">&lt;button type="button" class="btn btn-default"&gt;Default&lt;/button&gt;</code></pre>
        </p>
        <p>
            <button type="button" class="btn btn-primary">Primary</button><br>
            
        </p>
        <p>
          <button type="button" class="btn btn-success">Success</button><br>
          
        </p>
        <p>
          <button type="button" class="btn btn-danger">Danger</button><br>
          
        </p>
        <p>
          <button type="button" class="btn btn-link">Link</button><br>
          
        </p>
        <p>
          <button type="button" class="btn btn-info">Info</button><br>
          
        </p>
        <p>
          <button type="button" class="btn btn-warning">Warning</button><br>
          
        </p>
    </div>
    <h2></h2>
    <blockquote>
      .btn-lg.btn-sm  .btn-xs 
    </blockquote>
    <p>
        <button type="button" class="btn btn-primary">Default button</button><br>
        
    </p>
    <p>
        <button type="button" class="btn btn-primary btn-xs">Extra small button</button><br>
        
    </p>
    <p>
        <button type="button" class="btn btn-primary btn-sm">Small button</button><br>
        
    </p>
    <p>
        <button type="button" class="btn btn-primary btn-lg">Large button</button><br>
        
    </p>
</div></template>
<script>
export default {
    mounted() {
        const {hljs} = window;

        console.log(hljs);
        $('pre code').each(function(i, block) {
            hljs.highlightBlock(block);
        });

        const clipboard = new window.Clipboard('.clip', {
            text: function() {
                return $('.html').text();
            }
        });

        clipboard.on('success', function(e) {
            new MiniMsg({content: ''}).animation();
            e.clearSelection();
        });
    }
};
</script>
<style lang="css">
</style>



